<template>
  <div class="robot-monitor">
    <!-- 页面标题 -->
    <div class="page-header">
      <h1>🤖 Telegram机器人消息监控</h1>
      <p>实时监控Telegram机器人消息处理状态和结果</p>
    </div>

    <!-- 连接状态 -->
    <el-card class="connection-status" shadow="never">
      <template #header>
        <div class="card-header">
          <span>📡 连接状态</span>
          <el-button 
            :type="isConnected ? 'success' : 'danger'"
            :icon="isConnected ? 'Check' : 'Close'"
            @click="toggleConnection"
          >
            {{ isConnected ? '已连接' : '未连接' }}
          </el-button>
        </div>
      </template>
      
      <div class="status-info">
        <el-row :gutter="20">
          <el-col :span="8">
        <div class="status-item">
          <span class="label">WebSocket地址:</span>
          <span class="value">{{ wsUrl }}</span>
        </div>
        <div class="status-item">
          <span class="label">Telegram支持:</span>
          <el-tag type="success">已启用</el-tag>
        </div>
          </el-col>
          <el-col :span="8">
            <div class="status-item">
              <span class="label">连接状态:</span>
              <el-tag :type="isConnected ? 'success' : 'danger'">
                {{ isConnected ? '已连接' : '未连接' }}
              </el-tag>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="status-item">
              <span class="label">最后活动:</span>
              <span class="value">{{ lastActivity || '无' }}</span>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-card>

    <!-- 消息发送区域 -->
    <el-card class="message-sender" shadow="never">
      <template #header>
        <div class="card-header">
          <span>📤 发送测试消息</span>
          <el-tag type="info">支持Telegram群组消息</el-tag>
        </div>
      </template>
      
      <el-form :model="messageForm" label-width="100px">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="群组ID">
              <el-input v-model="messageForm.groupId" placeholder="输入群组ID" />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="用户ID">
              <el-input v-model="messageForm.userId" placeholder="输入用户ID" />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="消息类型">
              <el-select v-model="messageForm.type" placeholder="选择消息类型">
                <el-option label="Telegram消息" value="telegram_message" />
                <el-option label="群消息" value="group_message" />
                <el-option label="Ping" value="ping" />
                <el-option label="获取状态" value="get_status" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        
        <el-form-item label="消息内容">
          <el-input 
            v-model="messageForm.message" 
            type="textarea" 
            :rows="3"
            placeholder="输入消息内容，支持Telegram机器人命令如: /帮助, /预付+100, /下发-50, /查余额, /查成率1"
          />
        </el-form-item>
        
        <!-- Telegram消息专用字段 -->
        <div v-if="messageForm.type === 'telegram_message'" class="telegram-fields">
          <el-form-item label="Telegram更新数据">
            <el-input 
              v-model="messageForm.telegramUpdate" 
              type="textarea" 
              :rows="4"
              placeholder="输入Telegram Webhook更新数据 (JSON格式)"
            />
          </el-form-item>
        </div>
        
        <el-form-item>
          <el-button type="primary" @click="sendMessage" :disabled="!isConnected">
            发送消息
          </el-button>
          <el-button @click="clearForm">清空表单</el-button>
          <el-button type="success" @click="sendQuickCommand('help')">发送帮助命令</el-button>
          <el-button type="warning" @click="sendQuickCommand('ping')">发送Ping</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- 消息日志 -->
    <el-card class="message-log" shadow="never">
      <template #header>
        <div class="card-header">
          <span>📋 消息日志</span>
          <div>
            <el-button size="small" @click="clearLogs">清空日志</el-button>
            <el-button size="small" @click="toggleAutoScroll">
              {{ autoScroll ? '关闭自动滚动' : '开启自动滚动' }}
            </el-button>
          </div>
        </div>
      </template>
      
      <div class="log-container" ref="logContainer">
        <div 
          v-for="(log, index) in messageLogs" 
          :key="index"
          class="log-item"
          :class="log.type"
        >
          <div class="log-header">
            <span class="log-time">{{ log.timestamp }}</span>
            <el-tag :type="getLogTagType(log.type)" size="small">
              {{ getLogTypeText(log.type) }}
            </el-tag>
          </div>
          <div class="log-content">
            <pre>{{ log.content }}</pre>
          </div>
        </div>
        
        <div v-if="messageLogs.length === 0" class="empty-log">
          <el-empty description="暂无消息日志" />
        </div>
      </div>
    </el-card>

    <!-- 统计信息 -->
    <el-card class="statistics" shadow="never">
      <template #header>
        <div class="card-header">
          <span>📊 统计信息</span>
        </div>
      </template>
      
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.totalMessages }}</div>
            <div class="stat-label">总消息数</div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.successMessages }}</div>
            <div class="stat-label">成功消息</div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.errorMessages }}</div>
            <div class="stat-label">错误消息</div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="stat-item">
            <div class="stat-value">{{ statistics.uptime }}</div>
            <div class="stat-label">运行时间(秒)</div>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, onUnmounted, nextTick } from 'vue'
import { ElMessage } from 'element-plus'

// WebSocket连接
let ws: WebSocket | null = null
const isConnected = ref(false)
const lastActivity = ref('')
const wsUrl = 'ws://localhost:8789'

// 消息表单
const messageForm = reactive({
  groupId: 'test_group_001',
  userId: 'test_user_001',
  type: 'telegram_message',
  message: '/帮助',
  telegramUpdate: JSON.stringify({
    update_id: 123456789,
    message: {
      message_id: 1,
      from: {
        id: 123456789,
        is_bot: false,
        first_name: 'Test',
        last_name: 'User',
        username: 'testuser'
      },
      chat: {
        id: -1001234567890,
        title: 'Test Group',
        type: 'supergroup'
      },
      date: Math.floor(Date.now() / 1000),
      text: '/帮助'
    }
  }, null, 2)
})

// 消息日志
const messageLogs = ref<Array<{
  timestamp: string
  type: string
  content: string
}>>([])

const logContainer = ref<HTMLElement>()
const autoScroll = ref(true)

// 统计信息
const statistics = reactive({
  totalMessages: 0,
  successMessages: 0,
  errorMessages: 0,
  uptime: 0
})

// 连接WebSocket
const connectWebSocket = () => {
  try {
    ws = new WebSocket(wsUrl)
    
    ws.onopen = () => {
      isConnected.value = true
      lastActivity.value = new Date().toLocaleString()
      addLog('success', 'WebSocket连接已建立')
      ElMessage.success('WebSocket连接成功')
    }
    
    ws.onmessage = (event) => {
      lastActivity.value = new Date().toLocaleString()
      handleMessage(event.data)
    }
    
    ws.onclose = () => {
      isConnected.value = false
      addLog('warning', 'WebSocket连接已断开')
      ElMessage.warning('WebSocket连接断开')
    }
    
    ws.onerror = (error) => {
      isConnected.value = false
      addLog('error', 'WebSocket连接错误: ' + error)
      ElMessage.error('WebSocket连接错误')
    }
    
  } catch (error) {
    addLog('error', '连接WebSocket失败: ' + error)
    ElMessage.error('连接WebSocket失败')
  }
}

// 断开WebSocket
const disconnectWebSocket = () => {
  if (ws) {
    ws.close()
    ws = null
  }
}

// 切换连接状态
const toggleConnection = () => {
  if (isConnected.value) {
    disconnectWebSocket()
  } else {
    connectWebSocket()
  }
}

// 处理接收到的消息
const handleMessage = (data: string) => {
  try {
    const message = JSON.parse(data)
    addLog('info', JSON.stringify(message, null, 2))
    
    // 更新统计信息
    statistics.totalMessages++
    if (message.type === 'telegram_message_result' || message.type === 'group_message_result') {
      if (message.success) {
        statistics.successMessages++
      } else {
        statistics.errorMessages++
      }
    }
    
  } catch (error) {
    addLog('error', '解析消息失败: ' + error + '\n原始数据: ' + data)
  }
}

// 发送消息
const sendMessage = () => {
  if (!isConnected.value) {
    ElMessage.error('WebSocket未连接')
    return
  }
  
  if (!messageForm.message.trim()) {
    ElMessage.error('请输入消息内容')
    return
  }
  
  const data = {
    type: messageForm.type,
    message: messageForm.message,
    group_id: messageForm.groupId,
    user_id: messageForm.userId
  }
  
  ws?.send(JSON.stringify(data))
  addLog('sent', '发送消息: ' + JSON.stringify(data, null, 2))
  ElMessage.success('消息发送成功')
}

// 发送快速命令
const sendQuickCommand = (command: string) => {
  if (!isConnected.value) {
    ElMessage.error('WebSocket未连接')
    return
  }
  
  let data: any = {}
  
  switch (command) {
    case 'help':
      data = {
        type: 'group_message',
        message: '/帮助',
        group_id: messageForm.groupId,
        user_id: messageForm.userId
      }
      break
    case 'ping':
      data = { type: 'ping' }
      break
    case 'status':
      data = { type: 'get_status' }
      break
  }
  
  ws?.send(JSON.stringify(data))
  addLog('sent', '发送快速命令: ' + JSON.stringify(data, null, 2))
  ElMessage.success('快速命令发送成功')
}

// 添加日志
const addLog = (type: string, content: string) => {
  messageLogs.value.push({
    timestamp: new Date().toLocaleString(),
    type,
    content
  })
  
  // 限制日志数量
  if (messageLogs.value.length > 1000) {
    messageLogs.value = messageLogs.value.slice(-500)
  }
  
  // 自动滚动到底部
  if (autoScroll.value) {
    nextTick(() => {
      if (logContainer.value) {
        logContainer.value.scrollTop = logContainer.value.scrollHeight
      }
    })
  }
}

// 清空日志
const clearLogs = () => {
  messageLogs.value = []
  ElMessage.success('日志已清空')
}

// 切换自动滚动
const toggleAutoScroll = () => {
  autoScroll.value = !autoScroll.value
  ElMessage.info(autoScroll.value ? '已开启自动滚动' : '已关闭自动滚动')
}

// 清空表单
const clearForm = () => {
  messageForm.groupId = 'test_group_001'
  messageForm.userId = 'test_user_001'
  messageForm.type = 'group_message'
  messageForm.message = ''
}

// 获取日志标签类型
const getLogTagType = (type: string): 'primary' | 'success' | 'warning' | 'info' | 'danger' => {
  const typeMap: Record<string, 'primary' | 'success' | 'warning' | 'info' | 'danger'> = {
    'success': 'success',
    'error': 'danger',
    'warning': 'warning',
    'info': 'info',
    'sent': 'primary'
  }
  return typeMap[type] || 'info'
}

// 获取日志类型文本
const getLogTypeText = (type: string) => {
  const typeMap: Record<string, string> = {
    'success': '成功',
    'error': '错误',
    'warning': '警告',
    'info': '信息',
    'sent': '发送'
  }
  return typeMap[type] || '未知'
}

// 启动统计定时器
const startStatisticsTimer = () => {
  setInterval(() => {
    statistics.uptime++
  }, 1000)
}

// 组件挂载
onMounted(() => {
  connectWebSocket()
  startStatisticsTimer()
})

// 组件卸载
onUnmounted(() => {
  disconnectWebSocket()
})
</script>

